import { Container } from "@mui/material";
import useGatherDataFromApi from "Global/Hooks/useGatherDataFromApi";
import React from "react";
import { GetContactInformation } from "./Adapters/GetContactInformation";
import InformationCard from "./Components/InformationCard";

const ContactUs = () => {
  const FunctionArray = [
    {
      functions: GetContactInformation,
    },
  ];
  const { gatheredData } = useGatherDataFromApi(FunctionArray);

  return (
    <React.Fragment>
      <Container maxWidth="md">
        {gatheredData !== null ? (
          <InformationCard gatheredData={gatheredData} />
        ) : (
          <></>
        )}
      </Container>
    </React.Fragment>
  );
};

export default ContactUs;
