import React from "react";
import { Card, Stack } from "@mui/material";
import TextIconStack from "Global/Components/CommonComponents/TextIconStack/TextIconStack";
import { themeColors } from "Global/JsxThemeColor/JsxThemeColor";
import styles from '../styles/contact.us.module.scss'
import { PhoneEnabled } from "@mui/icons-material";
const InformationCard = ({ gatheredData }) => {
  const cachedIcons = JSON.parse(localStorage.getItem("icons"));
  const imgHeader = "data:image/svg+xml;base64,";



  // console.log('gatheredData-->',gatheredData)
 

  return (
    <React.Fragment>
      <Card
        elevation={2}
        sx={{
          mt: "2em",
          p: "1em",
          border: `1px solid ${themeColors.ColorPrimary}`,
          color: themeColors.ColorPrimary,
        }}
      >
        <Stack spacing={1} className={styles.infoContainer}>
          <a
            href={gatheredData?.WASite}
            style={{ color: themeColors.ColorPrimary }}
            target="_blank"
            rel="noopener noreferrer"
            
          >
            <TextIconStack
              Icon={imgHeader + cachedIcons?.GeneralIcon["SiteImage.svg"]}
              Gap="1em"
              Text={gatheredData?.WASite}
            />
          </a>
          <TextIconStack
            Icon={imgHeader + cachedIcons?.GeneralIcon["FaxImage.svg"]}
            Gap="1em"
            Text={gatheredData?.WAFax}
          />
          <TextIconStack
            Icon={imgHeader + cachedIcons?.GeneralIcon["Address.svg"]}
            Gap="1em"
            Text={gatheredData?.WAAddress}
          />
          <TextIconStack
            Icon={imgHeader + cachedIcons?.GeneralIcon["EmailImage.svg"]}
            Gap="1em"
            Text={gatheredData?.WAMail}
          />
          <TextIconStack
            Icon={imgHeader + cachedIcons?.GeneralIcon["Tel.svg"]}
            Gap="1em"
            Text={gatheredData?.WATelephone}
          />
        </Stack>
      </Card>
    </React.Fragment>
  );
};

export default InformationCard;
