import { Stack } from "@mui/material";
import { themeColors } from "Global/JsxThemeColor/JsxThemeColor";
import React from "react";
import { useTranslation } from "react-i18next";

const TextIconStack = ({ Icon, Gap, Text, isIconElement }) => {
  const { t, i18n } = useTranslation();
  return (
    <Stack
      spacing={Gap}
      direction={i18n?.language === "en" ? "row" : "row-reverse"}
    >
      {isIconElement ? (
        isIconElement
      ) : (
        <img width={20} height={20} src={Icon} alt={Text} />
      )}

      <p
        style={{
          color: `${themeColors.ColorPrimary} !important`,
        }}
      >
        {Text}
      </p>
    </Stack>
  );
};

export default TextIconStack;
